
export default [
  {
    path: '/',
    component: () => import('layouts/default'),
    children: [
      { path: '', component: () => import('pages/index') }
    ]
  },
  {
    path: '/case',
    component: () => import('layouts/caselayout'),
    children: [
      { path: '', component: () => import('pages/casePageIntro') }
    ]
  },
  {
    path: '/caseform',
    component: () => import('layouts/caselayout'),
    children: [
      { path: '', component: () => import('pages/casePageForm') }
    ],
    alias: 'casePageForm'
  },
  {
    path: '/gemof',
    component: () => import('layouts/gameoflayout'),
    children: [
      { path: '', component: () => import('pages/gamePageForm') }
    ],
    alias: 'gamePageForm'
  },
  {
    path: '/gemof2',
    component: () => import('layouts/gameoflayout'),
    children: [
      { path: '', component: () => import('pages/gamePageForm1') }
    ],
    alias: 'gamePageForm1'
  },
  { // Always leave this as last one
    path: '*',
    component: () => import('pages/404')
  }
]



